@use "../global/" as *;

.case-splide.splide {
  min-height: 471px;
  @include mq("md"){
    min-height: 488px;
  }
}

/* スライドの動き等速 */
.swiper-wrapper {
  position: relative;
  display: flex;
  height: 100%;
  // min-width: 471px;
  @include mq("md"){
    justify-content: space-between;
  }
  }

.contens_inner.splide__track{
  display: flex;
  overflow: hidden;
  position: relative;
  @include mq("md"){
    align-items: center;
    justify-content: flex-start;
    margin: 0 auto;
  }
}
/* 画像のサイズ調整 */
.swiper-slide {
    // flex-shrink: 0;
    width: 100%; // 1スライドを100%表示
    box-sizing: border-box;
    height: 100%;
    margin: 0 1.1875rem 0 -0.25rem;
    @media (min-width: 768px) {
      margin: initial;
        // 2枚横に並べる場合、gapはJSの spaceBetween で設定するのが主流
      }
}



.swiper__slide.trust__swiper{
  @include mq("md"){
    width: 15.5rem;
  }
}
.splide__slide.splide__slide-duplicate{
  height: inherit;
}

.swiper__slide img {
    display: block;
    align-self: center; /* 上下の中央揃え */
    max-width: 100%;
    // width: 100%;
    object-fit: cover; /* IE: not support */
  }

 /* 共通スタイル */
  
  /* ホバー時の変化（任意） */
  .btn-maru:hover {
    background-color: $sub;
  }

.splide__arrow--prev,
.splide__arrow--next{
    position: absolute;
    top: 50%;
    bottom: 0;
    z-index: 10;
      cursor: pointer;
      width: 20px;
      height: 20px;
      @include mq("md"){
        width: 34px;
        height: 34px;
      }
    }
.splide__arrow--next::after,
.splide__arrow--prev::after{
 content: "";
  position: relative;
  z-index: 2;
  display: block;
  background-color: transparent;
  border-color: $white;
  border-style: solid;
    width: 7px;
    height: 7px;
    top: 0;
    left: 0;
}

.splide__arrow--prev::after
 {
  transform: rotate(-135deg);
  border-width: 2px 2px 0 0;
  left: 2px;
  @include mq("md"){
    width: 12px;
    height: 12px;
        border-width: 4px 4px 0 0;
  }
}
.splide__arrow--next::after{
  transform: rotate(45deg);
  border-width: 2px 2px 0 0;
  left: -1px;
  @include mq("md"){
    width: 12px;
    height: 12px;
    border-width: 4px 4px 0 0;
  }
}

.splide__arrow--prev::before,
.splide__arrow--next::before{
  content: "";
  position: absolute;
  top: 0;
  right: 0 ;
  left: 0;
  bottom: 0;
  background-color: $main;
  border-radius: 50%;
  z-index: 1;
}

.splide__arrow--prev {
  left: -10px;
  @include mq("md"){

  }
}
.splide__arrow--next {
  right: 10px;
  @include mq("md"){
    right: 25px;
}
}
